* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #f8edeb;
    transition: 0.4s ease-in-out;
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

.scrollArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
}

.container {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

header {
    position: absolute;
    z-index: 99;
    width: 100%;
    padding: 60px;
    .header-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .logo {
            font-size: 2rem;
            font-weight: 900;
            letter-spacing: 2px;
            color: rgb(59, 58, 58);
        }
        nav {
            ul {
                margin: 0;
                padding: 0;
                display: flex;
                li {
                    list-style: none;
                    margin: 0 60px;
                    &.btn {
                        a {
                            color: #fff;
                            font-weight: 600;
                            background: #23232a;
                            padding: 16px 24px;
                            border-radius: 10px;
                        }
                    }
                    a {
                        text-transform: capitalize;
                        text-decoration: none;
                        color: rgb(44, 43, 43);
                        @media (max-width: 1440px) {
                            display: none;
                        }
                    }
                }
            }
        }
    }
}

.title {
    font-size: 3rem;
    text-align: center;
    width: 100%;
    margin: 0;
    color: rgb(59, 59, 59);
    padding-right: 50%;
    padding-bottom: 40%;
    span {
        display: block;
    }
    .small {
        font-size: 2rem;
    }
    .top {
        font-size: 2rem;
        margin-top: 10%;
    }
    @media (max-width: 1440px) {
        display: none;
    }
}

.loading {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #f8edeb;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.loading-bar-container {
    width: 100px;
    height: 32px;
    background: #f9dcc4;
}

.loading-bar {
    height: 32px;
    background: #ffb5a7
}